//---------  Site Header
.site-header {

    position: absolute;
    z-index: 100;
    left: 0;
    right: 0;
    padding-top: $spacing-unit;

    a {
        color: $text-color;
        border-bottom: none;
    }

    @include breakpoint (small-screen) {
    	padding-left: $small-screen-margin;
    }

}

.site-title {
    font-size: $font-size-7;
    font-family: $sans-serif;
    font-weight: 500;

    @include breakpoint(smallest-screen) {
        text-align: left;
        left:$small-screen-margin;
    }
}


.site-nav {
    position: absolute;
    right: 0;
    top: $spacing-unit;
}

.site-nav ul {

    font-size: $font-size-6;
    font-family: $sans-serif;
    font-weight: $font-weight-light;


        li {
            margin-left: 0;
            display: inline-block;

            &.active span {
                text-decoration: line-through;
                color: $text-color-secondary;
            }

        }

    @include breakpoint(smallest-screen) {
        font-size: $font-size-6;
    }

}

body.project {

    .site-nav ul li {
        color: $text-color-reverse;
        a { color: white;}
    }

    &.projectlist footer {
        background-color: $background-color-dark;
        color: white;
    }

}




// -------- Footer

footer {
    width: 100%;
    padding-top: $spacing-unit*2;
    padding-bottom: 0;
    text-align: right;

    p {
        font-size: $font-size-7;

        span { font-weight: $font-weight-medium; }

        @include breakpoint(smallest-screen) {
            font-size: $font-size-6;
        }
    }

    a {
        border-bottom: none !important;
    }

    @include breakpoint(smallest-screen) {
        text-align: left;
        padding-top: $spacing-unit;
    }

}




//--------- Content blocks and containers and grids


section[role='main'],
article[role='main'] {
    padding-top: $spacing-unit*2.6;
    padding-top:0;
    padding-bottom: 0;

	@include breakpoint(small-screen) {
        padding-top: 0;
    }
}

.section-top {
    padding-top: $spacing-unit*2.5;
}

.section-bottom {
    padding-bottom: $spacing-unit*1.6;
}

.container-row { width: 100%; }

.container,
%container {

    $containder-default-width: 90%;
    $container-small-screen-width: 84%;

    @extend %clearfix;
    max-width: $content-width-max;
    width: $containder-default-width;
    margin: 0 auto;
 	padding-bottom: $spacing-unit/2;


    &.container-small {
    	max-width: $content-width-small;
    	padding: 0;
    }

    &.container-medium {
        width: 70%;

        @include breakpoint(small-screen) {
            width: $container-small-screen-width;
        }
    }


    &.container-max {
    	max-width: 100%;
    	padding: 0;
    	width: 100%;
    }

    &.bottom-clear { padding-bottom: 0; }

    @include breakpoint(small-screen) {
        width: $container-small-screen-width;
    }


} // container



.container-centered,
%container-centered {
    text-align: center;
    width:70%;
    max-width: $content-width-max;
    margin-right: auto;
    margin-left: auto;

    @include breakpoint(small-screen) {
        width:85%;
    }
}


//--------- Formatting classes

.bg-color { background: $background-color; }
.bg-color-clear { background: white; }
.bg-color-dark { background: $background-color-dark;}
.bg-color-light { background: $background-color-light;}



.margin-bottom { 
    margin-bottom: $spacing-unit * 2; 
    @include breakpoint(smallest-screen) {
        margin-bottom: $spacing-unit;
    }
}

.margin-bottom-half { margin-bottom: $spacing-unit;}


.margin-top-xl { 
    margin-top: $spacing-unit * 2; 

    @include breakpoint(small-screen) {
        margin-top: $spacing-unit/2;
    }
}


.margin-top-half { margin-top: $spacing-unit/2;}

.margin-top { 
    margin-top: $spacing-unit; 

    @include breakpoint(small-screen) {
        margin-top: $spacing-unit/2;
    }
}
.margin-top-clear { margin-top: 0; }
.margin-bottom-clear { margin-bottom: 0; }
.bottom-clear { margin-bottom: 0; padding-bottom:0; }

.padding-right {
    padding-right: 13%;
    @include breakpoint(small-screen) {
        padding-right: 0;
    }
}


//---------- Image grid

.img-grid {
    padding:0;
    width: 100%;
    //max-width: $content-width * 1.3;
    margin: $spacing-unit auto;
    padding-left: $spacing-unit;
    padding-right: $spacing-unit;
    @extend %container;

    &.img-grid-small {
        max-width: $content-width-small;
    }

    .caption + img, 
    &:not(.img-grid-2) img + img { margin-top: $spacing-unit; }

	img {
		height: auto;
		border: none;
        width: 100%;
	}

	+ .img-grid { margin-top: 2%; }

    &.img-grid-hero {
        max-width: 100%;
        margin: 0;
        padding: 0;

        @include breakpoint(small-screen) {
            padding-right: 0;
            padding-left: 0;
        }
    }


    @include breakpoint(small-screen) {
        padding-left: $small-screen-margin;
        padding-right: $small-screen-margin;
    }
}

.img-grid-2 img { 
    float: left;
    width: 49%; 
    margin-right: 2%;

    &.last-unit { margin-right: 0; }
}


.img-grid-bg {
    background-color: $background-color-light;
}



//------------ PROJECT LIST

.project-list li {
    list-style-type: none;
    padding: 0.5rem 0;
}



//------------ CASE STUDY LAYOUT



.project-header {
    padding-top: $spacing-unit*2.5;
    padding-bottom: $spacing-unit*4;
    //width: 70%;

    @include breakpoint(small-screen) {
        //width: 100%;
        padding: $spacing-unit*1.2 0;
    }

}

.project-hero-img {
    padding-bottom: 0;
    margin-bottom: $spacing-unit/3;
}




// content
.project-content {
	padding-top: $spacing-unit;
    padding-bottom: $spacing-unit * 3;
}

.project-content > .container {
	@extend .container-small;
}


.video-embed {
	text-align: center;
	display: block;
	width: $content-width-small;
	height: $content-width-small * 0.562;
	margin: $spacing-unit 0;
	margin: auto;
	margin-top: $spacing-unit/4;
	margin-bottom: $spacing-unit;

	@include breakpoint(smallest-screen) {
		width: 100%;
		height: auto;
	}
}

.project-details .container {

    border-top: 1px solid $text-color-secondary;
    padding-top: $spacing-unit;
    padding-bottom: $spacing-unit*2;

}


//----------------- NEW GRIDS

.grid {

    .column {
        margin-right: 3%;
        float:left;

        img.shrink {
            @include breakpoint(smallest-screen) {
            width: 64%;
            margin-right: auto;
            margin-left:auto;
            margin-bottom: $spacing-unit;
            text-align: center;
            display:block;
        }
        }
    }

    .column-1 {
        width: 36%;

        @include breakpoint(smallest-screen) {
            width: 100%;
            margin-right: 0;
        }
    }

    .column-2 {
        width: 60%;

        @include breakpoint(smallest-screen) {
            width: 100%;
            margin-right: 0;
        }

    }

    .column-last { margin-right: 0; }


}

.photo-grid {

    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;

    .cell {

        width: calc(50% - 2rem);
        align-items: center;
        margin-bottom: 4rem;

        &.col-all { width:100%; }

        &.col-all-small {
            width: 60%; 
            margin: 0 20%;
            margin-bottom: 4rem;
        }

        img { 
            width: 100%;
        }

        .caption {
            margin-top: 0.5rem;
            font-size: 0.7rem;
        }
    }
}




