p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}


h1, h2, h3, h4, h5, h6 {
    margin-bottom: $spacing-unit / 4;
    font-weight: $font-weight-light;
    line-height: $line-height-headline;
}

%normalize-text {
    letter-spacing: normal;
    text-transform: none;
}

.text-centered {
    text-align: center;
}


h1, %h1 {
	font-size: $font-size-3;
}

h2, %h2 {
    font-size: $font-size-5;
    font-weight: $font-weight-medium;
}

h3, %h3 {
    font-size: $font-size-5;
}

h4, %h4 {
    font-size: $font-size-6;
    font-weight: $font-weight-medium;
}


h5, %h5 {
    font-size: $font-size-6;
}

h6, %h6 {
    font-size: $font-size-7;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
    letter-spacing: 0.06rem;
}


p, %p {
    text-transform: none;
    font-weight: $font-weight-light;
    letter-spacing: normal;
    font-size: $font-size-baseline;
    line-height: $line-height-body;

    &.display {
        font-size: $font-size-5;
        color: $text-color-secondary;
    }
}

p.secondary-text {
    letter-spacing: none;
    font-size: $font-size-6;
    //line-height: 1.2;
    //color: $text-color-secondary;
    font-weight: $font-weight-medium;
}

.accent-text {
    @extend %normalize-text;
    font-family: $serif;
    font-weight: $font-weight-light;
    margin-bottom: $spacing-unit / 2;
    color: $text-color;
    line-height: $line-height-headline;
    font-size: $font-size-4;
}

.accent-text-xl {
    @extend .accent-text;
    font-size: $font-size-3;
}

.accent-text-xxl {
    @extend .accent-text;
    font-size: $font-size-2;

    @include breakpoint(med-screen) {
        font-size: $font-size-3;
    }
}




.caption, %caption {
	font-family: $sans-serif;
	font-size: $font-size-7;
    color: $text-color-secondary;
    line-height: $line-height-caption;

    a {
        border-bottom: 1px dotted $text-color-secondary;
    }

    @include breakpoint(small-screen) {
        font-size: $font-size-6;
        line-height: $line-height-body;
    }
}


.caption-alt {
    @extend .caption;
    font-family: $monospace;
}


ul {
    list-style-position: outside; 
}

ul.list-horizontal {

    list-style:none;

    li {

        display: inline;
        font-family: $serif;
        font-size: $font-size-baseline;

        &:not(:first-child)::before {
            content: "\2014";
            margin-right: 0.15rem;
        }
    }

    &.list-secondary li {
        font-family: $sans-serif-black;
        font-size: $font-size-baseline;
    }
}


.align-right { text-align: right; }


// ------------ SPECIAL LAYOUTS: CASE STUDIES + PROJECT LINK GRID



.project-list {

    .title {
        font-size: $font-size-4;
        font-weight: $font-weight-bold;

        @include breakpoint(small-screen) {
            font-size: $font-size-5;
        }
    }

    .focus {
        @extend .caption;
        font-family: $monospace;
        color: $text-color-reverse;
        padding-left: 0.25rem;

        @include breakpoint(small-screen) {
            font-size: $font-size-7;
            display:block;
            padding-left:0;
            margin-bottom: 0.5rem;
        }
    }

}


.project-header p {

    &.caption {
        @extend %caption;
        margin-top: $spacing-unit/2;
        font-weight: $font-weight-medium;
        color: $text-color-reverse;
    }
}



.project-content {


    h2 {
        padding-right: 10%;
        margin-top: $spacing-unit;
    }

    ul:not(.list-overview) {
        font-family: $sans-serif;
        list-style-type: none;

        li::before {
            content: "â€¢";
            //color: $background-color;
            display: inline-block;
            width: 20px;
            margin-left: -20px;        
            padding-top: 4px;
        }  

        li { margin-left: 20px; }  
    }

} // case study




.project-content p.caption {
    @extend %caption;

    [aria-label="Back to content"] {
        font-size: 70%;
        border-bottom-width: 1px;
    }
}

.img-grid > .caption {
    display: inline-block;
    padding: 0 $spacing-unit;
    width: 100%;
    text-align: center;
    margin-top: $spacing-unit/4;
    color: $text-color;

    a { color: $text-color; }

    @include breakpoint(small-screen) {
        font-size: $font-size-baseline;
        padding: 0 $spacing-unit/6;
    }
}





.project-details {

    .caption { color: $text-color; }
}




// ------------ LINKS

a {
    text-decoration: none;
    color: inherit;
}


p a {
    color: inherit;    
    border-bottom: 2px solid $text-color;

    @include breakpoint(small-screen) {
        border-bottom-width: 1px;
    }

    &:hover {
        background-color: $text-color;
        border-bottom-color: $text-color;
        color:white;
        cursor: pointer;
    }
}


p a.no-underline { border-bottom: none;}

.button {
    display: inline-block;
    padding: $spacing-unit/4 $spacing-unit/1.5;
    background-color: darken($background-color-light, 5%);
    width: auto;
    border-radius: 50rem;
    border: none !important;
    margin: auto;
    margin-top: $spacing-unit/3;
    margin-bottom: $spacing-unit/3;
    font-weight: $font-weight-medium;

    &:hover {
        background-color: red;
    }
}


// -------------------- ORGANIZE LATER


.page-header {
    display: inline-block;
    margin-top: $spacing-unit;
    font-size: $font-size-baseline * 1.4;
    margin-bottom: $spacing-unit*2;
    color: blue;

    &:after {
        content: "\2014";
    }
}


.text-reverse {
    color: white;
    .accent-text, .accent-text-xl, .accent-text-xxl { color: white !important; }
}






